.products__overflow {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  max-height: 990px;
  background: rgba(0, 0, 0, 0.2);
  opacity: 1;
  transition: visibility $animations, opacity $animations;
  // overflow-y: scroll;
  // z-index: 998;
  z-index: 310;
  @include tablet {
    max-height: 995px;
  }
}

