.gallery {
    position: relative;
}

.gallery-position {
    position: absolute;
    top: -24px;
    left: 50%;
    transform: translateX(-50%);


@media screen and (min-width: 768px) {
        top: -164px;
        left: 50%;
        transform: translateX(-50%);
    }

@media screen and (min-width: 1200px) {
    top: -94px;
    left: 50%;
    transform: translateX(-50%);
}
}


.img_gallery {
  margin-left: auto;
  margin-right: auto;
  border-radius: 24px;
  overflow: hidden;

  @media screen and (max-width: 767px) {
    width: 279px;
    height: 130px;
  }

  @media screen and (min-width: 768px) {
    width: 704px;
    height: 327px;
  }

  @media screen and (min-width: 1200px) {
    width: 1013px;
    height: 422px;
  }
}


